<template>
	<div class="wrapper" ref="wrap">
		<div class="content">
			<slot></slot>
		</div>
	</div>
</template>
<script>
	import Bscroll from "better-scroll"
	export default{
		name:"scroll",
		props:{
           probe:{
            type:Number,
            default:0
           },
           pullUpLoad:{
            type:Boolean,
            default:false
           }
		},
		data(){
			return{
				scroll:null
			}
		},
		mounted(){
			this.$nextTick(()=>{
              if(!this.scroll){
				this.scroll = new Bscroll(this.$refs.wrap,{
                 click:true,
                 //是否监听滚动，0，1不监听，2监听，但手指松开时即使还在滚动但也不会持续监听，除非手指还在上面，3一直监听
                 //this.probe就希望由引用该插件的页面来控制是否监听滚动
                 probeType:this.probe,
                 //是否上拉开启事件
                 pullUpLoad:this.pullUpLoad
                })
              }else{
                  this.scroll.refresh();
              }
            
                //监听滚动
              this.scroll.on('scroll',(position) =>{
                  this.$emit('scroll',position);
              })
                //上拉加载
              this.scroll.on('pullingUp',()=>{
				this.$emit('pullingUp');
              })
			})
				
			
			
		},
		methods:{
			refresh(){
				//解决better-scroll在图片未加载完而滑动不流畅的问题，通过这刷新
               this.scroll && this.scroll.refresh();
			},
			//time=300就time这个参数如果没传就默认300
			scrollTo(x,y,time=300){
				//better-scroll里的一个方法，让你回到指定的位置，第三个参数是时间
               this.scroll && this.scroll.scrollTo(x,y,time);
			},
			finishPullUp(){
				//让其能继续上拉加载，因为默认只执行一次上拉事件的
				this.scroll && this.scroll.finishPullUp();
			}
		}
	}
</script>
<style scoped></style>